import React, { Component } from 'react';
import {
	Icon,
	Form,
	Button,
	Radio,
	Input,
	Upload,
	Divider
} from "antd";
import { connect } from "react-redux";
import { actionCreators } from '../store';
import 'moment/locale/zh-cn';
import "./index.scss";

class EditRandomMessage extends Component {

	componentDidMount() {
		let {location: {query}, initRandomMsgInfo} = this.props;
		query && initRandomMsgInfo(query)
	}

	render() {
		const { randomMessageInfo, message_info, content, currentIndex, changeContentValue, changeMessageValue, changeMessageType, uploadImg, addMessage, submitMessage, updateMessageItem, deleteMessageItem, saveMessage, cancelAction, history } = this.props;
		let isTextUploadImg, FormItemWrapper;
		if (content.picurl) {
			isTextUploadImg = <img style={{ width: "100px" }} src={content.picurl} alt="" />
		} else {
			isTextUploadImg = <Button><Icon type="upload" />  </Button>
		}
		if (message_info.type === 1) {
			FormItemWrapper = <Form.Item label="" className="text-message">
				<Input.TextArea autoSize={{ minRows: 6 }} value={content.description} placeholder="" onChange={e => changeContentValue(e.target.value, "description")} />
			</Form.Item>
		} else if (message_info.type === 0) {
			FormItemWrapper = <div>
				<Form.Item label="">
					<Upload name="imageFile" action="/reply/upload" onChange={uploadImg} showUploadList={false}>
						{isTextUploadImg}
					</Upload>
				</Form.Item>
				<Form.Item label="">
					<Input placeholder="" value={content.title} onChange={e => changeContentValue(e.target.value, "title")} />
				</Form.Item>
				<Form.Item label="" className="text-message">
					<Input.TextArea autoSize={{ minRows: 6 }} value={content.description} placeholder="" onChange={e => changeContentValue(e.target.value, "description")} />
				</Form.Item>
				<Form.Item label="">
					<Input placeholder="" value={content.url} onChange={e => changeContentValue(e.target.value, "url")} />
				</Form.Item>
			</div>
		}
		const formItemLayout = {
			labelCol: {
				sm: { span: 4 }
			},
			wrapperCol: {
				sm: { span: 20, offset: 0 }
			},
		};
		return (
			<div className="container">
				<div className="edit-random-message">
					<div className="kefu-form fl">
						<Form {...formItemLayout}>
							<Form.Item label="">
								<Input value={randomMessageInfo.remarks} placeholder="" onChange={e => changeMessageValue(e.target.value, "remarks")} />
							</Form.Item>
							<Form.Item label="ID">
								<Input value={randomMessageInfo.message_id} placeholder="ID" onChange={e => changeMessageValue(e.target.value, "message_id")} />
							</Form.Item>
							<Form.Item>
								<Radio.Group buttonStyle="solid" value={message_info.type} onChange={changeMessageType}>
									<Radio.Button value={0}></Radio.Button>
									<Radio.Button value={1}></Radio.Button>
								</Radio.Group>
							</Form.Item>
							{FormItemWrapper}
							{
								currentIndex > -1 ?
								<Button onClick={saveMessage}></Button> : 
								<Button onClick={addMessage}></Button>
							}
							<Divider />
							<Form.Item className="btn-wrapper">
								<Button type="primary" onClick={() => submitMessage(randomMessageInfo)}></Button>
								<Button style={{ marginLeft: 20 }} type="info" onClick={() => cancelAction(history)}></Button>
							</Form.Item>
						</Form>
					</div>
					{!!randomMessageInfo.message_array.length && <ul className="fr message-wrapper">
						{randomMessageInfo.message_array.map((item, index) =>
							<li key={index} className="cursor">
								<div className="head-other">
									<img src={item.contents[0].picurl} alt="" />
									<h3 className="title textEllipsis">{item.contents[0].title}</h3>
									<p className="ellipse-two">{item.contents[0].description}</p>
								</div>
								<div className="mask">
									<Button onClick={() => updateMessageItem(item, index)} type="link"></Button>
									<Button onClick={() => deleteMessageItem(index)} type="link"></Button>
								</div>
							</li>
						)}
					</ul>}
				</div>
			</div>
		)
	}

	componentWillUnmount() {
		this.props.resetState()
	}
	
}

const mapState = state => ({
	content: state.message.randomMsg.content,
	message_info: state.message.randomMsg.message_info,
	randomMessageInfo: state.message.randomMsg.randomMessageInfo,
	currentIndex: state.message.randomMsg.currentIndex,
});

const mapDispatch = dispatch => ({
	changeMessageValue(value, name) {
		dispatch(actionCreators.changeMessageValue(value, name))
	},
	changeMessageType(e) {
		dispatch(actionCreators.changeMessageType(e.target.value))
	},
	changeContentValue(value, name) {
		dispatch(actionCreators.changeContentValue(value, name))
	},
	uploadImg(info) {
		if (info.file.status === 'done') {
			let img = `http://t.dmmup.com/uploads/${info.file.response.filename}`;
			dispatch(actionCreators.changeContentValue(img, "picurl"));
			dispatch(actionCreators.changeContentValue(img, "local_picurl"));
		}
	},
	addMessage() {
		dispatch(actionCreators.addMessage())
	},
	updateMessageItem(item, index) {
		dispatch(actionCreators.updateMessageItem(item, index))
	},
	deleteMessageItem(index) {
		dispatch(actionCreators.deleteMessageItem(index))
	},
	saveMessage() {
		dispatch(actionCreators.saveMessage())
	}, 
	submitMessage(info) {
		dispatch(actionCreators.submitMessage(info))
	},
	cancelAction(history) {
		dispatch(actionCreators.cancelAction(history))
	},
	resetState() {
		dispatch(actionCreators.resetState())
	},
	initRandomMsgInfo(info) {
		dispatch(actionCreators.initRandomMsgInfo(info))
	}
});

export default connect(mapState, mapDispatch)(EditRandomMessage);